.promo {
  color: @white;
  margin-bottom: 22px;
  // background-color: @white;
  // background-image: url("../img/index-background-mobile@1x.jpg");
  // background-repeat: no-repeat;
  // background-size: 380px auto;
  // background-position: top center;
  // @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
  //   background-image: url("../img/index-background-mobile@2x.jpg");
  // }

  &::before {
    content: "";
    background-image: url("../img/index-background-mobile@1x.jpg");
    background-repeat: no-repeat;
    background-size: 320px auto;
    background-position: top center;
    background-color: @green-background;
    width: 100%;
    height: 305px;
    position: absolute;
    z-index: -1;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/index-background-mobile@2x.jpg");
    }
  }

  &__wrapper {
    .container;
  }

  &__title {
    display: block;
    font-weight: 400;
    font-size: 36px;
    line-height: 36px;
    text-align: center;
    box-sizing: border-box;
    padding: 25px 10px;
    margin: auto;
    text-transform: none;
  }

  &__slogan {
    display: block;
    text-transform: uppercase;
    font-size: 14px;
    line-height: 14px;
    text-align: center;
    margin: 0 auto 30px;
  }

  &__image {
    display: block;
    margin: 0 auto 3px;
  }

  &__button {
    width: auto;
    display: block;
  }
}

@media (min-width: @tablet-width) {
  .promo {
    background-image: none;
    padding-left: 120px;
    padding-bottom: 530px;
    margin-bottom: 0;
    box-sizing: border-box;
    position: relative;
    z-index: 1;

    &::before {
      content: none;
    }

    &__wrapper {
      padding: 0;
    }

    &__title {
      font-size: 60px;
      line-height: 60px;
      color: @black;
      text-transform: none;
      text-align: left;
      max-width: 70%;
      padding: 0;
      margin: 0;
      margin-bottom: 41px;
    }

    &__slogan {
      color: @black;
      font-size: 20px;
      line-height: 20px;
      text-align: left;
      margin-bottom: 50px;
    }

    &__button {
      font-size: 20px;
      line-height: 20px;
      width: 245px;
      padding: 15px 5px;
      padding-left: 0;
      margin: 0;
    }

    &__image {
      position: absolute;
      top: 200px;
      left: 30px;
      z-index: -1;
    }
  }
}

@media (min-width: @desktop-width) {
  .promo {
    .container-desktop;
    padding-left: 120px;
    padding-right: 525px;
    padding-top: 82px;
    padding-bottom: 190px;
    margin-bottom: 80px;
    position: static;

    &__wrapper {
      position: relative;
    }

    &__image {
      top: -80px;
      left: 370px;
    }

    &::before {
      content: "";
      position: absolute;
      background-image: url("../img/index-background-desktop@1x.jpg");
      background-size: 720px 100%;
      background-repeat: no-repeat;
      background-color: @green-background-dark;
      background-position: left;
      z-index: -1;
      display: block;
      width: 50%;
      height: 696px;
      top: 0;
      left: 50%;

      @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
        background-image: url("../img/index-background-desktop@2x.jpg");
      }
    }

    &__slogan {
      margin-bottom: 54px;
    }
  }
}
